#boardWrapper{
    padding: 40px;
  }
  
  #boardPaper{
    margin: auto;
    width: 90%;
    padding: 30px 20px;
  
    border: 2px solid #ccc;
  }
  
  .boardTitle{
    font-size: 30px;
    font-weight: bold;
    padding: 15px 0 ;
    border-bottom: 2px solid #ccc;
  }
  
  .boardInfo{
    display: flex;
    justify-content: space-between;
    padding: 30px 100px 30px 0;
    border-bottom: 2px solid #ccc;
    align-items: center;
  }
  
  .boardDate{
    display: flex;
    flex-direction: column;
  }
  
  .wrapDate{
    display: flex;
    justify-content: space-between;
    width: 200px;
  }
  .wrapDate>div:nth-of-type(1){
    font-weight: bold;
  }
  
  .boardContent{
    padding: 40px 0;
    min-height: 500px;
  }
  
  .btnArea{
    display: flex;
    justify-content: flex-end;
  }
  
  .btnArea >button{
    border: none;
    background-color: #455ba8;
    color: white;
    padding: 8px 20px;
    margin: 0 5px;
  }
  
  
  